@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  width: 100vw;
  font-family: "Poppins", sans-serif;
  background-color: #fff;
  background:url(/codingkapur/login-fintech/static/media/hero-img-2.d0be4db4.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(72, 72, 72);
}
@media screen and (max-width:600px){
  body {
    background-position: center;
  }
}
@media screen and (max-width:40px){
  body {
    background-position: top;
  }
}
.app__container {
  height: 100vh;
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
}

.nav__container {
  /* border: 1px solid #fff; */
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  background-color: rgba(0, 0, 0, 0.809);
  color: #fff;
}
.nav__logo {
  /* border: 1px solid #fff; */
  font-size: 2rem;
  font-weight: 600;
}
.logo-label {
    color: rgb(211, 144, 0);
}
.nav__links--pages {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.nav__pages--label {
  margin: 0 1rem;
  cursor: pointer;
  position: relative;
  text-align: center;
}
.nav__pages--label::after {
  display: block;
  content: "";
  position: absolute;
  top: 2rem;
  left: 0;
  height: 2px;
  width: 0%;
  background-color: rgb(211, 144, 0);
  transition: 0.3s ease-in;
  transform: scaleX(1);
}
.nav__pages--label:hover::after,
.nav__pages--label:active::after {
  width: 100%;
  transform: scaleX(1);
}
.nav__links {
  /* border: 1px solid #fff; */
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.nav__link {
  text-align: center;
  padding: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 20px;
  color: #fff;
  transition: 0.3s;
  cursor: pointer;
}
.nav__link:hover {
  background-color: rgb(211, 144, 0);
  color: rgb(255, 255, 255);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.302);
}
.nav__link:active {
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.302);
}
.sidebar__toggle--btn {
  display: none;
  cursor: pointer;
  transition: 1s ease;
}
.sidebar__toggle--btn:hover {
  transform: rotate(180deg);
}

@media screen and (max-width:900px){
  .nav__links--pages {
    display: none;
  }
  .nav__links {
    display: none;
  }
  .sidebar__toggle--btn {
    display: block;
  }
}

.hero__container {
  position: relative;
  height: 90vh;
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
}

.hero__text--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero__text--main {
  font-size: 5rem;
  font-weight: 600;
  text-transform: capitalize;
  line-height: 4rem;
  margin-bottom: 2rem;
  border-left: 4px solid rgb(255, 255, 255);
  padding: 1rem;
  color: rgb(211, 144, 0);
}
.transaction-label,
.portal-label {
  font-size: 4rem;
  color: rgb(255, 255, 255);
}

.hero__text--sub--container {
  border-right: 4px solid rgb(255, 255, 255);
  border-left: 4px solid rgba(255, 255, 255, 0);

  margin-bottom: 1rem;
  padding: 0 1rem;
}

.hero__text--sub {
  font-size: 1.2rem;
  font-weight: 500;
  color: rgb(255, 255, 255);
}
.hero__text--sub-2 {
  padding-left: 8rem;
}
.hero__text--sub-3 {
  padding-left: 13rem;
}
.hero__btn--container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  border-right: 4px solid rgb(255, 255, 255);
  padding: 0 1rem 0 20rem;
}
.hero__btn {
  text-align: center;
  padding: 0.5rem;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.302);
  color: rgb(0, 0, 0);
  transition: 0.3s;
  cursor: pointer;
}
.hero__btn:hover {
  background-color: rgb(211, 144, 0);
  color: rgb(255, 255, 255);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.302);
}
.hero__btn:active {
  box-shadow: 0 2px 3px rgba(118, 118, 118, 0.302);
}
@media screen and (max-width:1200px){
  .hero__btn--container {
    padding: 0 1rem;
  }
}
@media screen and (max-width: 500px){
  .hero__container {
    grid-template-columns: 1fr;
  }
  .hero__text--container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .hero__text--main {
    font-size: 3rem;
  }
  .transaction-label,
  .portal-label {
    font-size: 2.8rem;
  }
  
  .hero__text--sub--container {
    border-left: 4px solid rgb(255, 255, 255);
    border-right:none;
    margin-bottom: 3rem;
  }
  
  .hero__text--sub {
    font-size: 1.2rem;
  }
  .hero__text--sub-2 {
    padding-left: 0rem;
  }
  .hero__text--sub-3 {
    padding-left: 0rem;
  }
  .hero__btn--container {
    padding: 0 1rem 0 0rem;
  }
  .hero__btn {
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-radius: 20px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.302);
    color: rgb(0, 0, 0);
    transition: 0.3s;
    cursor: pointer;
  }
}
.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.7)
  );
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  box-shadow: 10px 10px 15px rgba(255, 255, 255, 0.111);
  z-index: 1;
}

.login__modal--container {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  width: 400px;
  border-radius: 10px;
  z-index: 5;
  background-color: #fff;
  box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.3);
  font-size: 1rem;
  font-weight: 700;
  color: #555;
}
@media screen and (max-width: 800px){
  .login__modal--container {
    left: 50%;
    width: 95%;
    border-radius: 10px;
    z-index: 10;
    background-color: #fff;
    box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.3);
    font-size: 1rem;
    font-weight: 700;
    color: #555;
    font-family: "Lato", sans-serif;
  }
}

.signin__label {
  text-align: center;
  text-transform: uppercase;
  padding: 2rem 0;
  border-bottom: 1px solid #555;
}
.modal__form--container {
  padding: 0 2rem;
  border: 1px solid #123;
}
.form__control--input {
  position: relative;
  border-bottom: 2px solid #adadad;
  margin: 2rem 0 0 0;
  margin-bottom: 1rem;
}

.form__input {
  width: 100%;
  padding: 0 5px;
  height: 2.2rem;
  font-size: 1rem;
  border: none;
  background: none;
  outline: none;
}
.form__control--label {
  position: absolute;
  top: 50%;
  left: 5px;
  color: #adadad;
  transform: translateY(-50%);
  font-size: 1rem;
  pointer-events: none;
  transition: 0.5s;
}
.modal__close--btn {
  font-size: 1.2rem;
  position: absolute;
  right: 2%;
  top: 2%;
  cursor: pointer;
}
.modal__close--btn:hover {
  transform: scale(1.1);
}
.form__control--input span::before {
  content: "";
  position: absolute;
  top: 2.2rem;
  left: 0;
  width: 0%;
  height: 2px;
  background: rgb(255, 175, 1);
  transition: 0.5s;
}

.form__input:focus ~ .form__control--label,
.form__input:valid ~ .form__control--label {
  top: -5px;
  color: rgb(85, 85, 85);
}

.form__input:focus ~ span::before,
.form__input:valid ~ span::before {
  width: 100%;
}

.forgot-password__label {
  color: #a6a6a6;
  transition: 0.3s;
  cursor: pointer;
  margin: -5px 0 20px 5px;
  color: #a6a6a6;
  font-size: 0.7rem;
}
.forgot-password__label:hover {
  color: #c43535;
  text-decoration: underline;
}
.error-message {
  opacity: 0;
  color: #c43535;
  font-size: 0.7rem;
  margin: 5px 0 10px 5px;
}
.btn-form {
  width: 100%;
  height: 2rem;
  background-color: #fff;
  border-radius: 25px;
  font-size: 1rem;
  color: #000000;
  font-weight: 700;
  cursor: pointer;
  outline: none;
  border: 1px solid #000;
}
.btn-form .btn-sign-in {
  margin-bottom: 1.5rem;
}
.btn-form:hover {
  transition: 0.1s;
  background-color: rgb(255, 175, 1);
  color: #fff;
}

.sign-up-form-text {
  margin: 2rem 0;
  text-align: center;
  font-size: 0.7rem;
  color: #666666;
}

.sign-up-form-text strong {
  color: #c43535;
  text-decoration: none;
}
.sign-up-form-text strong:hover {
  text-decoration: underline;
  cursor: pointer;
}

.btn-sign-in-container {
  margin: 0 0 30px 0;
}

.sidebar__container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 30;
  /* background-color: #000; */
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
  transform: translate(-100%);
  transition: 0.3s ease-in;
  background: linear-gradient(90deg, #000000, rgb(211, 144, 0));
  background-repeat: no-repeat;
  background-size: 400% 300%;
  color: #fff;
}

@keyframes gradient {
  0% {
    background-position: 100% 0%;
  }
  25% {
    background-position: 75% 0%;
  }
  50% {
    background-position: 50% 50%;
  }

  100% {
    background-position: 0% 100%;
  }
}
.show-sidebar {
  transform: translate(0);
  animation: gradient 1s ease;
}
.sidebar__top--container {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-size: 1.5rem;
}

.nav__links--pages-sidebar {
  font-size: 1.5rem;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0;
}
.sidebar__pages--label {
    margin-bottom: 1rem;
}
.nav__links-sidebar {
  font-size: 1.5rem;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.nav__link--sidebar {
width: 180px;
margin: 0.5rem 0;

}


